import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import salary from '@/components/salary'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/tools/salary',
      name: 'salary',
      component: salary
    }
  ],
})



